.menuHeader{
    width: 100%;
    height: 40px;
    background-color: #fff;
    border-bottom: 1.5px solid #cecece;
    box-shadow: 0 2px 2px -2px #333;
}
.menuHeader ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

.menuHeader > ul{
    display: flex;
    margin: 0 50px;
    position: relative;
    text-decoration: none;

}
.menuHeader > ul > li > span {
    
    display: block;
    line-height: 40px;
    cursor: pointer;
    padding: 0 20px;
    font-size: 14px;
    
}
.menuHeader > ul > li > span:hover{
    color: #2874f0;

}
.menuHeader > ul > li > ul{
    position: absolute;
    background: #fff;
    left: 0;
    right: 0;
    display: none;
    text-decoration: none;
    border: 1px solid #cecece; 
    z-index: 1;

}
.menuHeader > ul > li:hover ul{
    display: block;

}
.menuHeader > ul > li > ul >li{
    margin: 0 20px;
    float: left;
    text-decoration: none;
    min-width: 150px;
}
.menuHeader > ul > li > ul >li >a{
    font-weight: bold;
    font-size: 15px;
    text-decoration: none;
}
.menuHeader > ul > li > ul >li a{
    padding: 5px 0;
    font-size: 13px;
    text-decoration: none;
    display: inline-block;
}